// const md5 = require('../../components/md5.js');
// const rs = require('../../components/request.js');
let app = getApp().globalData;
let { appid,navBar,appsecret, rootUrl, imgUrl } = app;
export default {
	data() {
		return {
			imgUrl: app.imgUrl,
			img: [],
			contents:'',
			navBar: navBar
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		deletePhoto(e) {
		
			let { img } = this;
			img.splice(e, 1);
		},
		uploadImg() {
			var that = this;
			uni.chooseImage({
				count: 3, // 默认9
				sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success: function(res) {
					// 返回选定照片的本地文件路径列表，tempFilePath可以作为img标签的src属性显示图片
					var media_id = res.tempFilePaths;

					var feed = 'feed';
					var timeStamp = Math.round(new Date().getTime() / 1000);
					var obj = {
						appid: appid,
						type: feed,
						timeStamp: timeStamp
					};
					var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
					for (var i = 0; i < media_id.length; i++) {
						uni.uploadFile({
							method: 'POST',
							url: rootUrl + 'uploadImg', //此处换上你的接口地址
							name: 'img',
							header: {
								Accept: 'application/json',
								'content-type': 'application/json', //
								Authorization: uni.getStorageSync('token')
							},
							formData: {
								appid: appid,
								timeStamp: timeStamp,
								type: feed,
								img: media_id,
								sign: sign
							},
							filePath: media_id[i],
							success: function(res) {
								
								var img = JSON.parse(res.data);
							
								that.img.push(img.data);
							}
						});
					}
				}
			});
		},
		feedBack() {
			
		    var that = this
		  
		    var img = that.img;
		    let path=[];
		    for(let i of img){
		           path.push(i.path)
		    }
		    var contents = that.contents;
		    var timeStamp = Math.round(new Date().getTime() / 1000);
		
		    var obj = {
		      appid: appid,
		      contents: contents,
		      timeStamp: timeStamp,
		    }
		    var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		    var data = {
		      appid: appid,
		      contents: contents,
		      img: path,
		      timeStamp: timeStamp,
		      sign: sign,
		    }
		    rs.postRequests("feedBack", data, (res) => {
		      if (res.data.code == 200) {
		        
		         Toast('提交成功');
		         setTimeout(()=>{
		           uni.switchTab({
		             url: '/pages/index/index',
		           })
		         },1000)
		        
		      }else{
		         Toast(res.data.msg)
		      }
		    })
		  }
	}
};
